      </p>

      <h2>Componentes en Vue.js</h2>
      <p>
        Los componentes son instancias de Vue con un nombre determinado. Una
        forma comn de trabajar una aplicacin con esta arquitectura es mediante
        una estructura de rbol en la que cada seccin del sitio web est
        conformada por uno o varios componentes. Estos se declaran de la
        siguiente forma:
      </p>
      <p>Vue.component('my-component-name', {</p>
      <p>// ... opciones ...</p>
      <p>})</p>

      <p>
        Los componentes de una aplicacin deben ser registrados, dicho proceso
