      <h2>Qu son las directivas?</h2>
      <p>
        En esencia, una directiva son instrucciones o pequeos fragmentos de
        cdigo utilizados que pueden ser usados en HTML y la vez enviar
        instrucciones a Vue.js; estas permiten manipular el HTML y crear
        atributos dinmicos en Vue. Existen dos tipos de directivas en Vue.js:
      </p>

      <p>Directivas integradas como:</p>
      <ul>
        <li>
          v-text, que permite manipular la propiedad del elemento
