        </li>
        <li>v-html, que permite insertar contenido HTML plano.</li>
        <li>v-show, manipula la propiedad CSS display.</li>
        <li>
          v-if, v-else-if v-else, renderiza un elemento en funcin del
          resultado de una condicional.
        </li>
        <li>
          v-for, puede renderizar elementos mltiples veces dependiendo de la
          fuente de los datos.
        </li>
        <li>v-on, permite adjuntar un event listener a un elemento.</li>
        <li>
          v-bind, enlaza dinmicamente uno o ms atributos o propiedad de un
          componente a una expresin.
        </li>
        <li>
          v-model, crea un enlace de direccin doble entre las entradas de un
          formulario o salidas de componentes.
        </li>
      </ul>

      <p>
        Directivas personalizadas. Vue.js nos permite crear nuestras propias
